footer{
    position: relative;
    bottom: 0;
    font-size: 16px;
    background: #292D35;
    width: 100%;
    height: 471px;
    display: flex;
    justify-content: space-between;
    .footer-upper-wrapper{
        display: flex;
        width: 100%;
        height: 100%;
        .branding{
            width: 20%;
            display: flex;
            
            div{
                display: flex;
                justify-content: space-between;
                flex-direction: column;
            }
        }
        .resources{
            width: 20%;
            display: flex;
            .resource{
                h2{
                    font-size: 16px;
                }
                ul{
                   color: #484d56;
                }
            }
        }
    }
}